import React from 'react'
import { useSelector } from "react-redux";
import '../PageComponents/ResponsiveHeader/header.css'
import Slider from '../../Library/Slider/Slider'
import {Search} from '../FirstPageofSearchModule/SearchResizersAndSwitchers/SearchFront'
import {Helmet} from 'react-helmet'
import {useIntl,FormattedMessage} from 'react-intl'
// import ArkturDMClogoICO from '../../Library/Images/ArkturDMClogoICO.ico'
import {TopTours} from './TopTours'

import RotaryHeader from '../../Library/Icons/RotaryHeader_2.svg'
import ArkturDMClogo from '../../Library/Images/ArkturDMClogo.svg'


export const HomePage = () => {
    
    const {locale,messages} = useIntl();
    console.log('server_url: ', process.env.REACT_APP_URL);
    
    const promoCode = useSelector(state => state.promocode.promocode)
    const images = [
        "arktur.ua/sites/default/files/public/image/slider/Lavra_shutterstock_1545799133_small.jpg",
        "arktur.ua/sites/default/files/public/image/slider/Azov%20sea_small.jpg",
        "arktur.ua/sites/default/files/public/image/slider/carpathians_shutterstock_750654637_small.jpg",
        "arktur.ua/sites/default/files/public/image/slider/Odesa%20Opera%20House_shutterstock_179655773_small.jpg",
        "arktur.ua/sites/default/files/public/image/slider/KamenetsCastle_shutterstock_1621762234_small.jpg"
    
      ]

    return (
       <div>
           <Helmet>
               <title>Arktur DMC</title>
               {/* <link rel='shortcut icon' href={ArkturDMClogoICO} /> */}
               <link rel='shortcut icon' href='../../Library/Images/ArkturDMClogoICO.ico' />
           </Helmet>

           <h2 style={{
                        marginTop: '5vh', 
                       display:`${promoCode === '1497'?'none':'block'}`,
                       paddingBottom: '1vh',
                       color: 'rgb(122,6,8)', 
                       fontSize: '25px', 
                       fontFamily: 'Arial',
                       fontWeight: 'bold',
                       textAlign: 'center',
                      }}> 
                      {
                        messages&&messages.map((item)=>{
                          if(item.sitepage_region_id === 6&&item.sitepage_type_id === 20){
                            return (
                               <FormattedMessage id={item.title.map((item1)=>item1.text)}/>
                            )
                          }
                        })
                      }   </h2>

           {
             promoCode === '1497'? null : <Slider slides={images}/>
           }
           <Search />
           <TopTours/>
       </div>
    )
}
