import {
  REGISTER_SUCCESS,
  REGISTER_FAIL,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT,
} from "../actions/types";

console.log("user: ", localStorage.getItem("user"));
const user = JSON.parse(localStorage.getItem("user"));

const notRegisteredUser = {
  user_id: 1426,
  company_id: 1361,
  company_name: "hotelsukraine.com.ua"
};
const initialState = user
  ? { isLoggedIn: true, user }
  : { isLoggedIn: false,
      user: notRegisteredUser
    };

export default function (state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    case REGISTER_SUCCESS:
      return {
        ...state,
        isLoggedIn: false,
      };
    case REGISTER_FAIL:
      return {
        ...state,
        isLoggedIn: false,
      };
    case LOGIN_SUCCESS:
      return {
        ...state,
        isLoggedIn: true,
        user: payload.user,
      };
    case LOGIN_FAIL:
      return {
        ...state,
        isLoggedIn: false,
        user: notRegisteredUser,
      };
    case LOGOUT:
      return {
        ...state,
        isLoggedIn: false,
        user: notRegisteredUser,
      };
    default:
      return state;
  }
}
